.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  @include scale-picture;

  @include on-tablet {
    height: 32px;
    width: 32px;
  }

  @include on-pc {
    &--phone {
      &:hover {
        &::before {
          content: '+1 234 555-55-55';
          position: absolute;
          right: 40px;
          top: 8px;

          display: block;
          width: 139px;
          padding-right: 8px;
          text-decoration: none;
          color: $main-color;
        }
      }
    }
  }

  &--phone {
    background-image: url(../images/icons/phone-icon.png);
    position: relative;
  }

  &--burger-menu {
    background-image: url(../images/icons/burger-icon.png);

    &:hover {
      background-image: url(../images/icons/icon-burger-menu-hover.png);
    }
  }

  &--close {
    background-image: url(../images/icons/icon-close.png);
  }
}
